.main-nav__list {
  width: 320px;
  height: 223px;
  text-align: center;
  background-color: #81b3d2;
  padding: 0;
}

.site-list__item {
  width: 100%;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 55px;
  list-style: none;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
}

.site-list__item a {
  text-decoration: none;
  color: #ffffff;
}

.page-header__square {
  width: 320px;
  height: 84px;
  background-color: #ffffff;
  text-align: center;
}
